@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Outfit", sans-serif;
  overflow-x: hidden;
}

:root{
  --color-blue:#001730;
  --color-green: #4AD7D1;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
 
h1,
h4,
p,
a {
  color: #fff;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.btn {
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: rgb(248, 217, 15);
  color: #222;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;
}

.btn-light {
  /* background: rgba(255, 255, 255, 0.2); */
  background: transparent;
  color: #fff;
}

.btn:hover {
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  transition: 0.3s;
}

.contact-wrapper{
  position: relative;
}

.member-cards-wrapper{
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 2rem;
  flex-direction: column;
}

.card-content-portion{
  margin: 20px;
}

.cards-portion{
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 4rem;
  background: #001730;
  position: relative;

}

.nav-menu{
  display: flex;
}

.nav-menu li{
  padding: 0 1rem;
}

.nav-menu li a {
  font-size: 1.2rem;
  font-weight: 500;
}

.nav-menu li a:hover {
  color: #7bdcb5;
}

.hamburger{
  display: none;
}

@media screen and (max-width: 1040px){
  .nav-menu{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,0.9);
    position: absolute;
    top: 10rem;
    left: 100%;
    z-index: -1;
    transition: 0.3s ease-in;
  }

  .nav-menu.active{
    /* left: 0; */
    position: absolute;
    top: 10px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .nav-menu li{
    padding: 1rem 0;
  }

  .nav-menu li a{
    font-size: 2rem;
  }

  .hamburger{
    display: initial;
  }
  
}
