* {
  margin: 0;
  padding: 0;
}

.card-main-container {
  width: 280px;
  height: 350px;
  background-color: #001730;
  border-radius: 20px;
  margin-top: 20px;
  position: relative;
}

.card-main-container .card-inner-container {
  width: 100%;
  height: 100%;
  background-color: #4ad7d1;
  border-radius: 20px;
  transition: ease-in-out 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.card-main-container .card-upper-portion {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
}

.card-main-container .card-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: ease-in-out 0.3s;
}

.card-main-container .card-img-container {
  width: 150px;
  height: 150px;
  background-color: #001730;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  object-fit: cover;
  margin-bottom: 30px;
}

.card-main-container .card-img-container .card-image {
  width: 100%;
  width: 100%;
  border-radius: 50%;
  object-fit: cover;
  transition: ease-in-out 0.5s;
}

.card-main-container .card-link-icon {
  position: absolute;
  top: 305px;
  left: 120px;
}

.card-main-container .card-link-icon .icon-direct-class {
  color: #4ad7d1;
}

.card-main-container:hover .card-inner-container {
  height: 85%;
}

.card-main-container:hover .card-image {
  width: 80%;
  width: 80%;
}

.card-main-container:hover .card-content {
  color: #001730;
}
